
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XTextarea,
Group,
XInput,
Divider,
XButton,
Alert
},
data(){
return {
showAlert: false,
question: "",
answer: "武汉大学（Wuhan University），简称“武大”，是一所位于湖北武汉市的综合研究型大学，其办学源头溯源于清朝末期1893年湖广总督张之洞奏请清政府创办的自强学堂，已有一百多年历史，1913年改名国立武昌高等师范学校， 1926年组建国立武昌中山大学，1928年定名国立武汉大学，是民国四大名校之一。"
}
},
methods: {
submit(){
var _this = this;
if (this.question.length <= 0){
this.showAlert = true;
} else {
$.ajax({
type: "GET",
url: "https://api.jisuapi.com/iqa/query?appkey=148a7e2e4576476f&question="+_this.question,
dataType: "jsonp",
success: function(data){
if (data.status == 0) {
_this.answer = data.result.content;
} else {
alert(data.msg)
}
}
});

















}
}
}
}




